ion-action-sheet {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
  display: block;
  width: 100%;
  height: 100%;
}

.action-sheet-wrapper {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 10;
  display: block;
  margin: auto;
  width: 100%;
  max-width: 500px;
  transform: translate3d(0, 100%, 0);
}

.action-sheet-button {
  width: 100%;
}

.action-sheet-ios {
  text-align: center;
}

.action-sheet-ios .action-sheet-container {
  padding: 0 10px;
}

.action-sheet-ios .action-sheet-group {
  overflow: hidden;
  margin-bottom: 8px;
  border-radius: 13px;
  background: #f9f9f9;
}

.action-sheet-ios .action-sheet-group:last-child {
  margin-bottom: 10px;
}

.action-sheet-ios .action-sheet-title {
  padding: 1.5rem;
  border-bottom: 0.55px solid #d6d6da;
  border-radius: 0;
  font-size: 1.3rem;
  font-weight: 400;
  text-align: center;
  color: #8f8f8f;
}

.action-sheet-ios .action-sheet-button {
  margin: 0;
  padding: 18px;
  min-height: 5.6rem;
  border-bottom: 0.55px solid #d6d6da;
  font-size: 2rem;
  color: #007aff;
  background: transparent;
}

.action-sheet-ios .action-sheet-button:last-child {
  border-bottom-color: transparent;
}

.action-sheet-ios .action-sheet-button.activated {
  margin-top: -0.55px;
  border-top: 0.55px solid #ebebeb;
  border-bottom-color: #ebebeb;
  background: #ebebeb;
}

.action-sheet-ios .action-sheet-selected {
  font-weight: bold;
  background: #fff;
}

.action-sheet-ios .action-sheet-destructive {
  color: #f53d3d;
}

.action-sheet-ios .action-sheet-cancel {
  font-weight: 600;
  background: #fff;
}

.action-sheet-md .action-sheet-container {
  padding: .8rem 0;
  background: #fafafa;
}

.action-sheet-md .action-sheet-title {
  padding: 11px 16px 17px;
  font-size: 1.6rem;
  text-align: left;
  color: #757575;
}

.action-sheet-md .action-sheet-button {
  position: relative;
  overflow: hidden;
  padding: 0 16px;
  min-height: 4.8rem;
  font-size: 1.6rem;
  text-align: left;
  color: #222;
  background: transparent;
}

.action-sheet-md .action-sheet-button.activated {
  background: #f1f1f1;
}

.action-sheet-md .action-sheet-icon {
  margin: 0 32px 0 0;
  padding: 0;
  width: 2.3rem;
  font-size: 2.4rem;
  text-align: center;
  vertical-align: middle;
}

.action-sheet-md .action-sheet-group {
  overflow: hidden;
}

.action-sheet-md .action-sheet-group .button-inner {
  justify-content: flex-start;
}

.action-sheet-md .action-sheet-selected {
  font-weight: bold;
}

.action-sheet-wp .action-sheet-wrapper {
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.2);
}

.action-sheet-wp .action-sheet-title {
  padding: 11px 16px 17px;
  font-size: 2rem;
  text-align: left;
  color: #4d4d4d;
}

.action-sheet-wp .action-sheet-button {
  padding: 0 16px;
  min-height: 4.8rem;
  font-size: 1.5rem;
  text-align: left;
  color: #4d4d4d;
  background: transparent;
}

.action-sheet-wp .action-sheet-button.activated {
  background: #aaa;
}

.action-sheet-wp .action-sheet-icon {
  margin: 0 20px 0 0;
  padding: 0;
  width: 2.3rem;
  font-size: 2.4rem;
  text-align: center;
  vertical-align: middle;
}

.action-sheet-wp .action-sheet-container {
  padding: .8rem 0;
  background: #fff;
}

.action-sheet-wp .action-sheet-group .button-inner {
  justify-content: flex-start;
}

.action-sheet-wp .action-sheet-selected {
  font-weight: bold;
}

.action-sheet-wp .action-sheet-cancel {
  background: transparent;
}

ion-alert {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  contain: strict;
}

ion-alert.alert-top {
  align-items: flex-start;
  padding-top: 50px;
}

ion-alert input {
  width: 100%;
}

.alert-wrapper {
  z-index: 10;
  display: flex;
  flex-direction: column;
  min-width: 250px;
  max-height: 90%;
  opacity: 0;
  contain: content;
}

.alert-title {
  margin: 0;
  padding: 0;
}

.alert-sub-title {
  margin: 5px 0 0;
  padding: 0;
  font-weight: normal;
}

.alert-message {
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

.alert-input {
  padding: 10px 0;
  border: 0;
  background: inherit;
}

.alert-input::-moz-placeholder {
  color: #999;
}

.alert-input:-ms-input-placeholder {
  color: #999;
}

.alert-input::-webkit-input-placeholder {
  text-indent: 0;
  color: #999;
}

.alert-button-group {
  display: flex;
  flex-direction: row;
}

.alert-button-group-vertical {
  flex-direction: column;
  flex-wrap: nowrap;
}

.alert-button {
  z-index: 0;
  display: block;
  margin: 0;
  font-size: 14px;
  line-height: 20px;
}

.alert-tappable {
  -moz-appearance: none;
  -ms-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
  padding: 0;
  width: 100%;
  font-size: inherit;
  line-height: initial;
  text-align: left;
  text-align: start;
  background: transparent;
}

.alert-ios .alert-wrapper {
  overflow: hidden;
  max-width: 270px;
  border-radius: 13px;
  background-color: #f8f8f8;
  box-shadow: none;
}

.alert-ios .alert-head {
  padding: 12px 16px 7px;
  text-align: center;
}

.alert-ios .alert-title {
  margin-top: 8px;
  font-size: 17px;
  font-weight: 600;
}

.alert-ios .alert-sub-title {
  font-size: 14px;
  color: #666;
}

.alert-ios .alert-message,
.alert-ios .alert-input-group {
  padding: 0 16px 21px;
  font-size: 13px;
  text-align: center;
  color: inherit;
}

.alert-ios .alert-message {
  max-height: 240px;
}

.alert-ios .alert-message:empty {
  padding: 0 0 12px 0;
}

.alert-ios .alert-input {
  -moz-appearance: none;
  -ms-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  margin-top: 10px;
  padding: 6px;
  border: 0.55px solid #ccc;
  border-radius: 4px;
  background-color: #fff;
}

.alert-ios .alert-radio-group,
.alert-ios .alert-checkbox-group {
  overflow: scroll;
  max-height: 240px;
  border-top: 0.55px solid #dbdbdf;
  -webkit-overflow-scrolling: touch;
}

.alert-ios .alert-tappable {
  display: flex;
  min-height: 44px;
}

.alert-ios .alert-radio-label {
  overflow: hidden;
  flex: 1;
  order: 0;
  padding: 13px;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: initial;
}

.alert-ios [aria-checked=true] .alert-radio-label {
  color: #387ef5;
}

.alert-ios .alert-radio-icon {
  position: relative;
  order: 1;
  min-width: 30px;
}

.alert-ios [aria-checked=true] .alert-radio-inner {
  position: absolute;
  top: -7px;
  left: 7px;
  width: 6px;
  height: 12px;
  border-width: 2px;
  border-top-width: 0;
  border-left-width: 0;
  border-style: solid;
  border-color: #387ef5;
  transform: rotate(45deg);
}

.alert-ios .alert-checkbox-label {
  overflow: hidden;
  flex: 1;
  padding: 13px;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: initial;
}

.alert-ios [aria-checked=true] .alert-checkbox-label {
  color: initial;
}

.alert-ios .alert-checkbox-icon {
  position: relative;
  margin: 10px 6px 10px 16px;
  width: 21px;
  height: 21px;
  border-width: 0.55px;
  border-style: solid;
  border-radius: 50%;
  border-color: #c8c7cc;
  background-color: #fff;
}

.alert-ios [aria-checked=true] .alert-checkbox-icon {
  border-color: #387ef5;
  background-color: #387ef5;
}

.alert-ios [aria-checked=true] .alert-checkbox-inner {
  position: absolute;
  top: 4px;
  left: 7px;
  width: 4px;
  height: 9px;
  border-width: 0.55px;
  border-top-width: 0;
  border-left-width: 0;
  border-style: solid;
  border-color: #fff;
  transform: rotate(45deg);
}

.alert-ios .alert-button-group {
  flex-wrap: wrap;
  margin-right: -0.55px;
}

.alert-ios .alert-button {
  overflow: hidden;
  flex: 1 1 auto;
  margin: 0;
  min-width: 50%;
  height: 44px;
  border-top: 0.55px solid #dbdbdf;
  border-right: 0.55px solid #dbdbdf;
  border-radius: 0;
  font-size: 17px;
  color: #387ef5;
  background-color: transparent;
}

.alert-ios .alert-button:last-child {
  border-right: 0;
  font-weight: bold;
}

.alert-ios .alert-button.activated {
  background-color: #e9e9e9;
}

.alert-md .alert-wrapper {
  max-width: 280px;
  border-radius: 2px;
  background-color: #fafafa;
  box-shadow: 0 16px 20px rgba(0, 0, 0, 0.4);
}

.alert-md .alert-head {
  padding: 24px 24px 20px 24px;
  text-align: left;
}

.alert-md .alert-title {
  font-size: 22px;
}

.alert-md .alert-sub-title {
  font-size: 16px;
}

.alert-md .alert-message,
.alert-md .alert-input-group {
  padding: 0 24px 24px 24px;
  color: rgba(0, 0, 0, 0.5);
}

.alert-md .alert-message {
  max-height: 240px;
  font-size: 15px;
}

.alert-md .alert-message:empty {
  padding: 0;
}

.alert-md .alert-input {
  margin: 5px 0 5px 0;
  border-bottom: 1px solid #dedede;
  color: #000;
}

